Lannie Spann McBride
File:  style.css
Author: Andrew J. Lewis 
Twitter: @ajlmusic
*/
@import url("https://fonts.googleapis.com/css?family=Cabin&display=swap");
:root {
  --primary-bg-color: rgba(244, 166, 127, 1);
  --secondary-bg-color: rgba(255, 239, 224, 1);
  --showcase-bg-color: rgba(150, 170, 230, 1);
  --showcase-bg-color2: rgba(244,166,127,1);
  --overlay-color: rgba(14, 20, 0, 0.85);
  --menu-speed: 0.75s;
  --bg-info: rgba(52, 52, 52, 1);
  --bg-info2: rgb(167, 167, 167);
  --bg-info3: rgb(230,122,161);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  background: black;
}
body {
  font-family: "Cabin", sans-serif;
  min-width: 400px;
}
.logo {
  font-size: 1.3rem;
  text-transform: uppercase;
}
.header {
  height: 60px;
  border-bottom: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.parallax {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 85vh;
}

.content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
}
.content h1,
p {
  font-size: 3.3em;
  text-shadow: 2px 2px 4px #000000;
  text-align: center;
}
.content p {
  font-size: 2.3rem;
}
.primary {
  /* height: 300vh; */
 
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-color: ivory;
  background-image: url("./assets/41_Lannie_portrait_2015.jpg");
  background-origin: border-box;
  background-size: cover;
  background-position-y: 0px;
  /* background-image: url("./assets/4_Home_Playing_Keyboard.jpg"); */
  /* background-size: cover; */
}

.primary h1 {
 
  font-size: 1.5em;

}
.secondary {
  display: grid;
 padding: 5em;
  grid-auto-flow: row;
  gap: 2em;
  place-items: center;
  justify-content: space-around;
  background-image: url('./assets/4_Home_Playing_Keyboard.jpg');
  overflow: auto;
}


iframe {
  width: auto;
  height: auto;
  
}
@media (min-width: 800px) {
  .primary {
    background-image: url("./assets/lspann-home.jpg");
    flex-direction: row;
  }
  .primary h1 {

    width: 60vw;
 
    font-size: 4em;
    margin-bottom: 1em;
    padding-right: 1rem;
    text-align: center;
  }

  .secondary {
    grid-auto-flow: column;
    overflow: auto;
    
  }
  iframe {
    width: 560px;
    height: 315px;
  }

}


.worship {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  min-height: 450px;
}
.about {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  min-height: 450px;
  color: white;
  padding: 5rem;
  background: rgb(81, 71, 73);
}
.about h1 {
  font-size: 3.3em;
  text-shadow: 2px 2px 4px #000000;
  text-align: center;
}

.about p {
  font-size: 1.3rem;
}

.directions {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-around;
  min-height: 500px;
  background-color: var(--bg-info);
}

.additional-content {
  padding: 3em;
  background-color: antiquewhite;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}
.additional-content > div {
  margin: 0 3em;
}
.additional-content h1 {
  padding: 0.25em;
  margin-bottom: 0.5em;
  background: var(--primary-bg-color);
  color: white;
  font-size: 1.5em;
  text-align: center;
  min-width: 350px;
}

.quick-links {
  width: 350px;
  height: 350px;
}
.quick-links li {
  background-color: white;
  list-style: none;
  padding: 0.5em 1em;
  text-align: center;
}
.quick-links a {
  text-decoration: none;
  font-size: 1.2em;
}

footer {
  min-height: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column-reverse;
  color: white;
}
footer > div {
  margin: 1em;
}

/* Main Container */
.container {
  height: 100vh;
  margin: auto;
  overflow: auto;
  padding: 0;
  /* background: url('../img/CollegeHill-Ext-Large.jpg') no-repeat center center/cover; */
}

.container > div {
  margin: auto;
}

.showcase {
  /* background: var(--showcase-bg-color); */
  color: white;
  min-height: 50vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.showcase:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.showcase .showcase-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100%;
}

.showcase h1 {
  font-size: 2.3em;
}

.showcase p {
  font-size: 1.3rem;
}

.btn {
  display: inline-block;
  border: none;
  background: var(--primary-color);
  color: #fff;
  padding: 0.75rem 1.5rem;
  margin-top: 1rem;
  transition: opacity 1s ease-in-out;
  text-decoration: none;
}
.btn:hover {
  opacity: 0.5;
  color: black;
}

.social-container {
  display: grid;
  grid-auto-flow: column;
}

.social {
  margin: 0.125em;
  width: 45px;
  height: 45px;
  font-size: 1.5em;
  color: white;
  border-radius: 50%;
  display: grid;
  place-items: center;
}
.facebook {
  background: #3b5998;
}
.twitter {
  background: #1da1f2;
}
.google-plus {
  background: #dd4b39;
}
.linkedin {
  background: #0077b5;
}
.whatsapp {
  background: #25d366;
}



.calendar {
  font-size: 4rem;
}



.content-section-wrapper {
  width: 100%;
  padding: 2em;
  /*   min-height: 400px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  color: white;
}

.color-info {

  background-color: var(--bg-info3);
}

.mailing-list input[type="email"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.mailing-list input[type="submit"] {
  width: 100%;
  background-color: var(--bg-info);
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.auth {
  min-height: 80vh;
  display: grid;
  place-items: center;
  grid-auto-flow: column;
}
input {
  width: 100%;
  font-size: 1.5em;;
}