/* MENU STYLES */
.menu-container {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
  }
  .menu-container .menu-title {
    position: absolute;
    top: 50px;
    left: 70px;
    z-index: 10;
    cursor: pointer;
    width: 50px;
    height: 50px;
    opacity: 0;
    color: white;
  }
  .menu-container .toggler {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    cursor: pointer;
    width: 50px;
    height: 50px;
    opacity: 0;
  }
  
  .menu-container .toggle-btn {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 60px;
    height: 60px;
    padding: 1rem;
    background: var(--primary-color);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  /* Hamburger Line */
  .menu-container .toggle-btn > div {
    position: relative;
    -webkit-flex: none;
            flex: none;
    width: 100%;
    height: 2px;
    background: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    transition: all 0.4s ease;
  }
  
  /* Hamburger Lines - Top & Bottom */
  .menu-container .toggle-btn > div::before,
  .menu-container .toggle-btn > div::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: -10px;
    width: 100%;
    height: 2px;
    /* background: inherit; */
    background: #fff;
  }
  
  /* Moves Line Down */
  .menu-container .toggle-btn > div::after {
    top: 10px;
  }
  
  /* Toggler Animation */
  /* .menu-container .toggler:checked + .toggle-btn > div {
    transform: rotate(135deg);
  } */
  
  /* Turns Lines Into X */
  .menu-container .toggler:checked + .toggle-btn > div {
    background: none;
  }
  
  .menu-container .toggler:checked + .toggle-btn > div:before
   {
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    transition-duration: var(--menu-speed2);
  }
  
  .menu-container .toggler:checked + .toggle-btn > div:after {
    top: 0;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    transition-duration: var(--menu-speed2);
  }
  
  /* Rotate On Hover When Checked */
  .menu-container .toggler:checked:hover + .toggle-btn > div {
    /* transform: rotate(225deg); */
  }
  
  /* Show Menu */
  .menu-container .toggler:checked ~ .menu {
    visibility: visible;
  }
  
  .menu-container .toggler:checked ~ .menu > div {
    -webkit-transform: scale(1);
            transform: scale(1);
    transition-duration: var(--menu-speed);
  }
  
  .menu-container .toggler:checked ~ .menu > div > div {
    opacity: 1;
    transition:  opacity 0.4s ease 0.4s;
  }
  
  .menu-container .menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .menu-container .menu > div {
    background-color: var(--overlay-color);
    /* background-image: url('https://images.unsplash.com/photo-1525362081669-2b476bb628c3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=934&q=80') no-repeat center center/cover; */
    border-radius: 50%;
    width: 200vw;
    height: 200vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: none;
            flex: none;
    -webkit-align-items: center ;
            align-items: center ;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: all 0.4s ease;
  }
  
  .menu-container .menu > div > div {
    text-align: center;
    max-width: 90vw;
    max-height: 100vh;
    opacity: 0;
    transition: opacity 0.4s ease;
  }
  
  .menu-container .menu > div > div > ul > li {
    list-style: none;
    color: #fff;
    font-size: 1.8rem;
    padding: 1rem;
  }
  
  .menu-container .menu > div > div > ul > li > a {
    color: inherit;
    text-decoration: none;
    transition: color 0.4s ease;
  }
  
